import React from "react";
import Home from './Home';
import Banner from './Banner';
import Header from './Header';
import Follow from './Follow';
import Footer from './Footer';
import SplashScreen from './SplashScreen';
import Newsletter from './Newsletter';
import Ambassador from './Ambassador';
import Slider from './Slider';
import Gallery from './Gallery';
import Team from './Team';
import Roadmap from './Roadmap';
import Presale from './Presale';
import Provenance from './Provenance';
import NotFound from './404';
import GetWhitelisted from './GetWhitelisted';
import TermsAndConditions from './TermsAndConditions';
import PrivacyPolicy from './PrivacyPolicy';
import Faqs from './Faqs';
import AmbassadorsSection  from './AmbassadorsSection';
import ProvenanceSection  from './ProvenanceSection';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

function App()  {
    return (
        <div class  = "App">
            <Switch>
                <Route exact path="/">
                    <Header />
                    <SplashScreen />
                    {/* <Banner />  */}
                    {/* <Slider /> */}
                    <Roadmap />
                    <Team />
                    {/* <AmbassadorsSection /> */}
                    {/* <ProvenanceSection />  */}
                    <Newsletter />
                    <Follow />
                    <Footer />
                </Route>
                <Route exact path = "/get-whitelisted">
                    <Header />
                    <GetWhitelisted />
                    <Follow />
                    <Footer />
                </Route>
                <Route exact path="/presale">
                    <Header />
                    <div id = "presale-page" class = "gnft-pa-section">
                        <div class = "they-can-mint">
                            <div class = "container text-center">
                                <h1>Mint your Gratitude Gang NFT!</h1>
                                <p class = "description">Fill out the form below to verify if you are whitelisted.</p>
                            </div>
                            <Presale />
                        </div>
                        <div class = "no-can-mint">
                            <div class = "container text-center">
                                <img src = "assets/images/minting-icon.png"></img>
                                <h1  class = "mt-3 d-none">Congratulations! </h1>
                                <h1>You already minted your GratitudeGang NFT.</h1>
                                <p class = "description">Please wait for further announcements regarding the public sale.</p>
                            </div>
                        </div>
                    </div>
                    <Follow />
                    <Footer />
                </Route>
                {/*  <Route exact path="/provenance">
                    <Header />
                    <Provenance />
                    <Follow />
                    <Footer />
                </Route>  */}
                <Route exact path="/ambassador">
                    <Header />
                    <div id = "ambassador-page" class = "gnft-pa-section">
                        <div class = "container text-center">
                            <h1>Mint your Gratitude Gang NFT!</h1>
                            <p class = "description">Fill out the form below to verify if you are an ambassador..</p>
                        </div>
                        <Ambassador />
                    </div>
                    <Follow />
                    <Footer />
                </Route>
                {/* <Route exact path="/gallery">
                    <Header />
                    <Gallery />
                    <Follow />
                    <Footer />
                </Route> */}
                <Route exact path="/terms-and-conditions">
                    <Header />
                    <TermsAndConditions />
                    <Follow />
                    <Footer />
                </Route>
                <Route exact path="/privacy-policy">
                    <Header />
                    <PrivacyPolicy />
                    <Follow />
                    <Footer />
                </Route>
                <Route exact path="/faqs">
                    <Header />
                    <Faqs />
                    <Follow />
                    <Footer />
                </Route>
                <Route>
                    <Header />
                    <NotFound />
                    <Follow />
                    <Footer />
                </Route>
            </Switch>
        </div>
    )
}

export default App;
