import React from "react";
import Metadata from "./Metadata";
import LazyLoad from 'react-lazyload';
const backgroundUnique = [...new Set(Metadata.map(post => post.attributes[0].value))];
const leafUnique = [...new Set(Metadata.map(post => post.attributes[1].value))];
const petalsUnique = [...new Set(Metadata.map(post => post.attributes[2].value))];
const eyesUnique = [...new Set(Metadata.map(post => post.attributes[3].value))];
const eyesAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[4].value))];
const headAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[5].value))];
const neckAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[6].value))];
const mainAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[7].value))];



function Gallery()  {
    return (
        <section id="gnft-gallery" data-aos="fade-up">
            <div class  = "container">
                <div class = "row">
                    <div class = "col-12 col-md-3">
                        <div class = "attribute-select-wrapper d-flex flex-column ">
                        <h2 class = "filter-heading">Filter</h2>
                            <select id = "attribute-select-background" class = "attribute-select">
                                <option value="All" class= "d-none"  selected>Background</option>
                                {
                                    backgroundUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-leaf" class = "attribute-select mt-2">
                                <option value="All" class = "d-none" selected>Leaf</option>
                                {
                                    leafUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-petals" class = "attribute-select mt-2">
                                <option class = "d-none" value="All" selected>Petals</option>
                                {
                                    petalsUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-eyes" class = "attribute-select mt-2">
                                <option value="All" selected>Eyes</option>
                                {
                                    eyesUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-eyes-acc" class = "attribute-select mt-2">
                                <option class = "d-none" value="All" selected>Eyes Accessories</option>
                                {
                                    eyesAccessoriesUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-head-acc" class = "attribute-select mt-2">
                                <option class = "d-none" value="All" selected>Head Accessories</option>
                                {
                                    headAccessoriesUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-neck-acc" class = "attribute-select mt-2">
                                <option class = "d-none" value="All" selected>Neck Accessories</option>
                                {
                                    neckAccessoriesUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <select id = "attribute-select-main-acc" class = "attribute-select mt-2">
                                <option class = "d-none" value="All" selected>Main Accessories</option>
                                {
                                    mainAccessoriesUnique.map(post => {
                                        return (
                                            <>
                                                <option value = {post}>{post}</option>
                                            </>
                                        )
                                    })

                                }
                            </select>
                            <input id= "attribute-input-id" class = "mt-2" type = "number" min = "0" max = "2222" placeholder = "By ID"></input>
                            <button class = "reset-gallery-filters">Reset Filters</button>
                        </div>
                    </div>
                    <div class = "col-12 col-md-9">
                        <h2 id = "heading-gallery">Gallery<span class = "d-none d-lg-inline">a total of 2222 sunflowers</span></h2>
                        <div class = "row">
                            {
                                
                                Metadata.map(post => {
                                        return (
                                            <>
                                            <div id = {"gallery-id-" +post.edition} class = "gallery-img-wrapper col-12 col-sm-6 col-lg-4 mt-3" data-all = "All Values" data-background={post.attributes[0].value} data-leaf={post.attributes[1].value}  data-petals={post.attributes[2].value} data-eyes={post.attributes[3].value} data-eyes-accessories={post.attributes[4].value} data-head-accessories={post.attributes[5].value} data-neck-accessories={post.attributes[6].value} data-main-accessories={post.attributes[7].value} data-gratitude-id={post.edition}>
                                                <div class = "img-wrapper">
                                                <LazyLoad>
                                                    <img height="100%" class = "img-fluid" src = {post.image} alt = {post.name} />
                                                </LazyLoad>
                                                </div>
                                                <h3 class = "text-center mt-2">Gratitude</h3>
                                                <p class = "edition text-center">No. {post.edition}</p>
                                            </div>
                                            </>
                                        )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Gallery;
