import React from "react";

function Faqs()  {
    return (
        <section id = "gnft-faqs" class = "gnft-tc-pp">
            <div class = "container">
                <h1 class = "text-center text-lg-left" data-aos="fade-in">Frequently Asked Questions</h1>
                <div class = "row justify-content-between mt-1">
                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-right">
                            <h2 class = "mb-3">What is an NFT?</h2>
                            <p>An NFT, or Non-Fungible Token, transform digital works of art and other collectibles into one-of-a-kind, verifiable assets that are easy to trade on the blockchain. In other words, they're like collectible baseball cards that go up and down in value do to the rarity of the art, the access to utilities (benefits) that can be used in the real world (or the metaverse) and serve as "tickets" to events involving the community.</p>
                        </div>
                    </div>

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-left">
                            <h2 class = "mb-3">What does it mean to MINT?</h2>
                            <p>To "mint" means your digital art becomes a part of the Ethereum blockchain - a public ledger that is unchangeable and tamper-proof.</p>
                        </div>
                    </div>

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-right">
                            <h2 class = "mb-3">When does GRATITUDE GANG NFT go on sale?</h2>
                            <p>Gratitude Gang NFTs WHITELIST will be on 2-22-22 (February 22nd, 2022) at 22:22 EST (10:22 pm) and PUBLIC SALE will be on 2-27-22 (February 27th, 2022) at 22:22 EST(10:22 pm). Both events will occur on the website www.gratitudegang.io. . Once sold out, they will be available for purchase on OpenSea (www.opensea.io)</p>
                        </div>
                    </div>

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-left">
                            <h2 class = "mb-3">What is the focus of this project?</h2>
                            <p>GRATITUDE GANG is the first NFT collection powered by GRATITUDE, driven by SOCIAL IMPACT, and good for overall WELL-BEING. Our aim as a project and as community is to be the champions of positivity, service, and personal growth both in the METAVERSE and especially IN REAL LIFE.</p>
                        </div>
                    </div>

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-right">
                            <h2 class = "mb-3">Are more NFTs released in the future?</h2>
                            <p>YES. We're here for the long game. This is the first project and features an artist from the Philippines. We will be collaborating with artists from all over the world including Kenya, Nepal, Romania, and more. Stay Tuned!</p>
                        </div>
                    </div> 

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-left">
                            <h2 class = "mb-3">Where is my NFT stored and will it be there permanently?</h2>
                            <p>It will "live" in OpenSea. Make sure to start an account by going to the website www.opensea.io</p>
                        </div>
                    </div>

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-right">
                            <h2 class = "mb-3">Do I own the NFT after minting?</h2>
                            <p>YES. You're GRATITUDE GANG sunflower is yours until you decide to sell it on the opensea market. But then again, WHY WOULD YOU?</p>
                        </div>
                    </div>

                    <div class = "col-12 col-lg-6 mt-4">
                        <div class = "question-answer-wrapper" data-aos="fade-left">
                            <h2 class = "mb-3">How many NFTs will be available?</h2>
                            <p>The will be 2,222 unique sunflowers available during this initial launch.</p>
                        </div>
                    </div>

                </div>
            </div>
        </section>
    )
}

export default Faqs;